import { memo } from 'react';
import { Link } from 'react-router-dom';
// import Box from '../../Common/Box';

const card = {
  marginBottom: '1.875rem',
  backgroundColor: '#fff',
  transition: 'all .5s ease-in-ou',
  position: 'relative',
  border: '0px solid transparent',
  borderRadius: '0.75rem',
  boxShadow: '0px 12px 33px 0px rgb(62 73 84 / 8%)',
  height: 'calc(100% - 30px)',
};

const cardBody = {
  padding: '0.8rem',
  flex: '1 1 auto',
  minHeight: '1px',
};

const imgWidth = {
  width: '100%',
  marginBottom: '0.8rem',
};

const textStyle = {
  fontSize: '24px',
  color: '#000',
  fontWeight: '600',
};

const contentStyle = {
  color: '#b7b7b7',
  fontSize: '14px',
  lineHeight: '22px',
  marginTop: '5px',
  overflow: 'hidden',
  paddingBottom: '10px',
};

const CoinItem = memo(() => (
  <div className='col-xl-3 col-lg-6 col-md-6 col-sm-6'>
    <div className='card' style={card}>
      <div className='card-body' style={cardBody}>
        <div className='new-arrival-product'>
          <div className='new-arrivals-img-contnent'>
            <img
              className='img-fluid'
              src='https://upload-hotbit-io.oss-ap-southeast-1.aliyuncs.com/files/NU-USDT-s.png'
              alt=''
              style={imgWidth}
            />
          </div>
          <div className='new-arrival-content text-center mt-3'>
            <h4>
              <Link to='/eventDetail' className='text-black' style={textStyle}>
                NU Goal:360000
              </Link>
            </h4>
            <span className='' style={contentStyle}>
              Type of Token Supported: USDT
              <br />
              Ended by: 2019-12-07 18:00:00
            </span>
            <hr />
            <span className='price'>100% Raised</span>
          </div>
        </div>
      </div>
    </div>
  </div>
));

export default CoinItem;
