import { useState } from 'react';
import { Link } from 'react-router-dom';

import MainLayout from '../../layouts/MainLayout';
import Box from '../../components/Common/Box';
import FormInput from '../../components/Forms/FormInput';
import FormButton from '../../components/Forms/FormButton';
import FormCheckbox from '../../components/Forms/FormCheckbox';

const SignupScreen = () => {
  const [formValues, setFormValues] = useState({
    email: '',
    phone: '',
    password: '',
    password1: '',
    name: '',
    lastname: '',
    citizenship: false,
    identityType: '',
    identityNumber: '',
    day: '',
    month: '',
    year: '',
    country: '',
    operator: '',
    agreeToPolicies1: false,
    agreeToPolicies2: false,
    agreeToPolicies3: false,
  });

  const [selectedType, setSelectedType] = useState(0);

  const handleChange = (e) => {
    const { name, value } = e.target;

    setFormValues({
      ...formValues,
      [name]: value,
    });
  };

  const handleCheckboxChange = (e) => {
    const { name, checked } = e.target;

    setFormValues({
      ...formValues,
      [name]: checked,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
  };

  return (
    <MainLayout>
      <div className='flex flex-center flex-column'>
        <h1 className='form-title center login-title'>REGISTER</h1>
        <div className='login no-select'>
          <Box>
            <div className='box-vertical-padding box-horizontal-padding'>
              <div>
                <div className='form-logo center'>
                  <img src='/images/logo_tupe.png' alt='Crypto Exchange' draggable='false' />
                </div>

                <p className='form-desc center'>
                  {/* Please enter the information below. your activation information to your phone
                  number we will send */}
                </p>
                <form className='form' onSubmit={handleSubmit} noValidate>
                  <div className='form-elements'>
                    <div className='flex flex-center actionBar'>
                      <div
                        role='button'
                        className={
                          selectedType === 0
                            ? 'flex flex-center full-width pointer selected'
                            : 'flex flex-center full-width pointer'
                        }
                        tabIndex={0}
                        onClick={() => setSelectedType(0)}
                        onKeyDown={() => setSelectedType(0)}
                      >
                        <img src='/images/email.png' alt='email' draggable='false' />
                        <h1 className='form-title'>Email</h1>
                      </div>
                      <div className='button_line' />
                      <div
                        role='button'
                        className={
                          selectedType === 1
                            ? 'flex flex-center full-width pointer selected'
                            : 'flex flex-center full-width pointer'
                        }
                        tabIndex={0}
                        onClick={() => setSelectedType(1)}
                        onKeyDown={() => setSelectedType(1)}
                      >
                        <img src='/images/phone.png' alt='phone' draggable='false' />
                        <h1 className='form-title'>Phone</h1>
                      </div>
                    </div>
                    {selectedType === 0 ? (
                      <div className='form-line box-horizontal-padding'>
                        <div className='full-width'>
                          <label htmlFor='email'>E-Mail Address *</label>
                          <FormInput
                            type='email'
                            name='email'
                            value={formValues.email}
                            placeholder='Enter Email Address'
                            onChange={handleChange}
                          />
                        </div>
                      </div>
                    ) : (
                      <div className='form-line box-horizontal-padding'>
                        <div className='full-width'>
                          <label htmlFor='email'>Phone Number *</label>
                          <FormInput
                            type='email'
                            name='email'
                            value={formValues.email}
                            placeholder='Enter Phone Number'
                            onChange={handleChange}
                          />
                        </div>
                      </div>
                    )}
                    {selectedType === 0 ? (
                      <div className='form-line box-horizontal-padding'>
                        <div className='full-width'>
                          <label htmlFor='email'>E-Mail Verification Code *</label>
                          <FormInput
                            type='email'
                            name='email'
                            value={formValues.email}
                            placeholder='Enter Email Verification Code'
                            onChange={handleChange}
                          />
                        </div>
                      </div>
                    ) : (
                      <div className='form-line box-horizontal-padding'>
                        <div className='full-width'>
                          <label htmlFor='email'>SMS Code *</label>
                          <FormInput
                            type='email'
                            name='email'
                            value={formValues.email}
                            placeholder='Enter your SMS Code'
                            onChange={handleChange}
                          />
                        </div>
                      </div>
                    )}
                    <div className='form-line box-horizontal-padding'>
                      <div className='full-width'>
                        <label htmlFor='password'>Your Password *</label>
                        <FormInput
                          type='password'
                          name='password'
                          value={formValues.password}
                          placeholder='Enter Your Password'
                          onChange={handleChange}
                        />
                      </div>
                    </div>
                    <div className='form-line box-horizontal-padding'>
                      <div className='full-width'>
                        <label htmlFor='password1'>Confirm your password *</label>
                        <FormInput
                          type='password'
                          name='password1'
                          value={formValues.password1}
                          placeholder='Confirm your password'
                          onChange={handleChange}
                        />
                      </div>
                    </div>
                    <div className='form-line box-horizontal-padding'>
                      <div className='full-width'>
                        <label htmlFor='name'>Referrer</label>
                        <FormInput
                          type='text'
                          name='name'
                          value={formValues.name}
                          placeholder='Referrer'
                          onChange={handleChange}
                        />
                      </div>
                    </div>
                    <div className='form-line box-horizontal-padding'>
                      <div className='full-width'>
                        <FormCheckbox
                          name='citizenship'
                          checked={formValues.citizenship}
                          text='I agree the Terms and policy'
                          onChange={handleCheckboxChange}
                        />
                      </div>
                    </div>
                    <div className='form-line box-horizontal-padding'>
                      <div className='buttons'>
                        <FormButton type='submit' text='Register' onClick={handleSubmit} />
                      </div>
                    </div>
                    <div className='form-line box-horizontal-padding'>
                      <div className='center'>
                        <p>
                          Do you have an account? <Link to='/'>sign in</Link>.
                        </p>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </Box>
        </div>
      </div>
    </MainLayout>
  );
};

export default SignupScreen;
