import SiteLayout from '../../layouts/SiteLayout';
import Header from '../../components/Header/Header';

const AddressManagementScreen = () => (
  <SiteLayout>
    <Header icon='sort' title='Market' />
    <div className='common-width usercenter-content'>
      <div className='addr-manage'>
        <div className='addr_cell1'>
          <p className='addr-title' data-nsfw-filter-status='swf'>
            Address Management
          </p>
          <div className='setting-box'>
            <input type='text' placeholder='Search Coins' className='seach-ipt' />
            <button className='delete' type='button'>
              Delete
            </button>
            <button className='add_addr_btn' type='button'>
              <i className='fa fa-plus-circle' />
              Add Withdrawal Address
            </button>
          </div>
        </div>
        <table className='list-tab'>
          <tr>
            <th>Blockchain</th>
            <th>Address</th>
            <th>MEMO / TAG / Paymentid</th>
            <th>Notes</th>
            <th>Other</th>
          </tr>
        </table>
      </div>
    </div>
  </SiteLayout>
);

export default AddressManagementScreen;
