import { useState, useEffect } from 'react';

import MainLayout from '../../layouts/MainLayout';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';

import Events from '../../components/Widgets/Events/Events';
import CoinVertical from '../../components/Widgets/Coin/CoinVertical';
import CoinHorizontal from '../../components/Widgets/Coin/CoinHorizontal';
import CoinItem from '../../components/Widgets/CoinItem/CoinItem';

const EventsScreen = () => {
  const [keyword, setKeyword] = useState('');
  const [coinInfo, setCoinInfo] = useState(null);

  useEffect(() => {
    const coinData = {
      id: 1,
      name: 'Bitcoin',
      symbol: 'BTC',
      change: '-%3.28',
      currency: 'TRY',
      exchange: 'BTC/TRY',
      weight: '104k',
      financialRate: '-0.0252%/hr',
      icon: 'https://icons-for-free.com/iconfiles/png/512/btc+coin+crypto+icon-1320162856490699468.png',
      amount: '18.783,33',
      description: `Bitcoin is a cryptocurrency invented in 2008 by an unknown person or group using the name Satoshi Nakamoto. Released as an open source software in 2009`,
    };

    setCoinInfo(coinData);
  }, []);

  const handleSearchValue = (e) => {
    const { value } = e.target;

    setKeyword(value);
  };

  const handleSearchSubmit = (e) => {
    e.preventDefault();
  };

  return (
    <MainLayout>
      <div className='content'>
        <Header title='Events' />
        <div className='flex flex-destroy'>
          <div className='content-30 box-right-padding'>
            <Events />

            {coinInfo && <CoinVertical item={coinInfo} />}
          </div>
          <div className='content-70 flex-1'>
            {coinInfo && (
              <CoinHorizontal
                item={coinInfo}
                searchValue={keyword}
                searchOnChange={handleSearchValue}
                searchSubmit={handleSearchSubmit}
              />
            )}

            <div className='flex flex-destroy flex-space-between center'>
              <div className='flex-1 box-right-padding'>
                <CoinItem />
              </div>
              <div className='flex-1 box-right-padding'>
                <CoinItem />
              </div>
              <div className='flex-1 box-right-padding'>
                <CoinItem />
              </div>
              <div className='flex-1'>
                <CoinItem />
              </div>
            </div>
            <div className='flex flex-destroy flex-space-between center'>
              <div className='flex-1 box-right-padding'>
                <CoinItem />
              </div>
              <div className='flex-1 box-right-padding'>
                <CoinItem />
              </div>
              <div className='flex-1 box-right-padding'>
                <CoinItem />
              </div>
              <div className='flex-1' />
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </MainLayout>
  );
};

export default EventsScreen;
